import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import { route } from 'preact-router';
import Markdown from 'preact-markdown';
import linkstate from 'linkstate';

import mu from '../../lib/musher-util';

@observer
class AppRaceDetailNotifications extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
		this.inputRef = {};
        this.updateTimer = {};
    }

    subscribeNotificationTopic = async (field) => {
		const { userStore } = this.props.stores;
        const { user } = userStore;
        const { notificationTopics = {} } = user;
        const value = 1;
        await userStore.setNotificationTopic({
            field: `notificationTopics.${field}`,
            value,
        });
    }

    render() {
		const { userStore, raceStore, appState } = this.props.stores;
		const { user = {}, teams = [], isAdmin } = userStore;
        const { language = 'en' } = user;
        const { notificationTopics = {} } = user;

		const { race = {} } = this.props;

        const topic = `race-${race.id}`;
        if (notificationTopics && notificationTopics[topic]) {
            return '';
        }

        return (<>
            <div class='row'>
                <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-2'>
                    <button class='btn btn-block btn-info btn-lg' onClick={() => this.subscribeNotificationTopic(topic)}>
                        <i class='fas fa-bell' /> <Text id='race.notifications'>Subscribe to race notifications</Text>
                    </button>
                </div>
            </div>
        </>);
    }
}

export default AppRaceDetailNotifications;
