@observer
class AppRaceDetailTracking extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
            reloadLastEpoch: Math.floor(new Date().getTime() / 1000),
        };
        this.updateTimer = {};
    }

    loadLiveRaceResults = async (props = this.props, focusOnMushers) => {
        const { raceStore } = props.stores;
        const { race } = raceStore;
        await raceStore.getRaceTrackerData(race.raceTrackerRace);
        raceStore.getRaceTrackerActiveTeams();
    }

    loadRaceResults = async (props = this.props, teamIds, focusOnMushers) => {
        const { raceStore } = props.stores;
        const { raceid } = props;

        await raceStore.loadWorkoutSummary(teamIds, raceid);
        raceStore.getWorkoutSummaryActiveTeams(focusOnMushers);
    }

    loadRace = async (props = this.props) => {
        this.setState({ isLoading: true });
        const { raceStore, appState } = props.stores;
        const { raceid } = props;
        await raceStore.load(raceid);
        const { race } = raceStore;
        const { focusOnMushers } = appState;

        const allTeamIds = race.classes.filter(currentClass => currentClass.contestants && currentClass.contestants.length > 0).map((currentClass) => {
            return currentClass.contestants.filter(musher => musher.team > 0).map(musher => musher.team);
        }).flat();

        if (allTeamIds.length > 0) {
            await raceStore.loadTeams(allTeamIds.join(','));
            if (race.liveRace > 0) {
                await this.loadLiveRaceResults(props, focusOnMushers);

            } else {
                await this.loadRaceResults(props, allTeamIds.join(','), focusOnMushers);
            }
        }
        raceStore.getAllClassesSortedBy();

        // Set reload timer
        clearTimeout(this.updateTimer.reload);
        this.updateTimer.reload = setTimeout(async () => {
            const reloadLastEpoch = Math.floor(new Date().getTime() / 1000);
            this.setState({ reloadLastEpoch });
            this.loadRace(props);
        }, RELOAD_INTERVAL_IN_SEC * 1000);

        this.setState({ isLoading: false });
    }

    setLeaderboardMode = (e) => {
        const leaderboardMode = e.target.parentElement.dataset.mode || e.target.dataset.mode;
        this.setState({ leaderboardMode })
    }

    chooseRaceClass = (e) => {
        const filterByraceClass = e.target.value;
        this.setState({ filterByraceClass })
    }

    toggleShowLeaderBoard = (hide) => {
        const { showLeaderBoard } = this.state;
        if (hide === true) {
            this.setState({ showLeaderBoard: false });
        } else {
            this.setState({ showLeaderBoard: !showLeaderBoard });
        }
    }

    toggleShowNews = (hide) => {
        const { showNews } = this.state;
        if (hide === true) {
            this.setState({ showNews: false });
        } else {
            this.setState({ showNews: !showNews });
        }
    }

    componentWillUnmount() {
        clearTimeout(this.updateTimer.reload);
        clearTimeout(this.updateTimer.tick);
    }

    componentDidMount() {
        this.loadRace();
        const { width, height } = getWindowDimensions();
        if (width < 800) {
            this.toggleShowLeaderBoard(true);
            this.toggleShowNews(true);
        }
    }

    onMarkerClick = ({ title, bib, email, e }) => {
        const { showModal } = this.state;
        this.setState({
            showModal: !showModal,
            modalTitle: title,
            modalBib: bib,
            modalEmail: email,
        });
    }

    toggleModal = () => {
        const { showModal } = this.state;
        this.setState({
            showModal: !showModal,
        });
    }

    // Overlay functions
    onTeamClick = (e, teamid) => {
        e.preventDefault();
        e.stopPropagation();
        this.setState({
            showTeam: teamid,
        });
        return false;
    }

    hideTeam = () => {
        this.setState({
            showTeam: null,
        });
    }

    onUserClick = (e, userid) => {
        e.preventDefault();
        e.stopPropagation();
        return false;
    }

    onDogClick = (e, dogid, dogs) => {
        e.preventDefault();
        e.stopPropagation();
        this.setState({
            showDog: dogid,
            dogs,
        });
        return false;
    }

    hideDog = () => {
        this.setState({
            showDog: null,
        });
    }

    // /Overlay functions

    back = () => {
        const { history } = this.props.stores;
        history.goBack();
    }

    render() {
        const { userStore, raceStore, appState } = this.props.stores;
        const { user = {}, teams = [], isAdmin } = userStore;
        const { language = 'en' } = user;

        const { raceid, path } = this.props;
        const { focusOnMushers = [], infoOnMushers = [] } = appState;
        const { leaderboardMode, filterByraceClass, showLeaderBoard, showNews, reloadLastEpoch,
            showModal, modalTitle, modalBib, modalEmail,
            showTeam, showDog,
        } = this.state;

        const { activeTeams, teamList, waypoints, waypoints2, waypoints3, zoomToMarkers, classes, race, racetrackerData } = raceStore;

        const musher = modalBib ? raceStore.getMusherByBibId(modalBib) : raceStore.getMusherByBibEmail(modalEmail);
        const team = raceStore.getTeamById(musher.team);

        return (<>
            {showTeam && <Overlay>
                <TeamsPublicDetail stores={this.props.stores} teamid={showTeam} marginTop={'0px;'} marginBottom={'0px;'} back={this.hideTeam} hideTeam={this.hideTeam} />
            </Overlay>}
            {showDog && <Overlay>
                <DogPublicDetail stores={this.props.stores} dogid={showDog} marginTop={'0px;'} marginBottom={'0px;'} back={this.hideDog} dogs={team.dogs} />
            </Overlay>}
        
            <div class='container-fluid position-relative p-0' style={`margin-top: ${MARGIN_TOP}; height: calc(100vh - ${'130px'});`}>
                <Trackmap
                    stores={this.props.stores}
                    raceid={raceid}
                    waypoints={waypoints}
                    waypoints2={waypoints2}
                    waypoints3={waypoints3}
                    zoomToMarkers={zoomToMarkers}
                    fullscreenMap={true}
                    onMarkerClick={this.onMarkerClick}
                    // key={`heatmap-${racetrackerData.map(e => e.bib).join('-')}`}
                    // mapid={`heatmap-${racetrackerData.map(e => e.bib).join('-')}`}
                />

                <div class='position-absolute' style='top: 15px; left: 50px; z-index: 9999;'>
                    <button class='btn btn-link pl-0 fixed-top text-primary'
                        style={`top: ${MARGIN_TOP_BACK}; left: 5px; font-size: 35px; width: 45px;`}
                        onClick={this.back}>
                        <i class='fas fa-arrow-circle-left' />
                    </button>
                    <div class='logo ml-3 clearfix text-white'>
                        <img src={`./assets/homerun-race-logo-2-small.png`} height='37' class='float-left' />
                        <a href={`/app-races/${race.id}/results`}>
                            <span class='font-weight-bold text-dark' style='font-size: 1.4em;'>{race.title}</span>
                        </a>
                    </div>
                </div>

                <div class='position-absolute' style='top: 55px; left: 5px; z-index: 9999; width: 30px; height: 30px;'>
                    <ReloadTimer stores={this.props.stores} classNames='float-right ml-2' reloadLastEpoch={reloadLastEpoch} />
                </div>
                {showModal && <>
                    <div
                        class='position-absolute'
                        style='top: 0px; left: 0px; z-index: 9999; width: 100%; height: 100%; background: rgba(0, 0, 0, 0.3);'
                        onClick={this.toggleModal}
                    >
                        <div
                            class='position-absolute bg-light border rounded rounded-lg pt-5 pb-3 px-2'
                            style='top: 50%; left: 50%; transform: translate(-50%, -50%); width: 80vw; max-width: 80vw; max-height: 80vh; overflow: auto;'
                        >
                            <button class='btn btn-link float-right position-absolute' style='top: 0px; right: 0px;'>
                                Close
                            </button>

                            <MusherInfo
                                stores={this.props.stores}
                                bib={modalBib}
                                email={modalEmail}
                                onTeamClick={this.onTeamClick}
                                onUserClick={this.onUserClick}
                                onDogClick={this.onDogClick}
                            />
                        </div>
                    </div>
                </>}
            </div>
        </>);
    }
}

export default AppRaceDetailTracking;
