    doParseInput = async (data, classId) => {
        const lines = String(data).split('\n');
        let headers = [];
        const jsonData = [];

        // Get headers
        const firstLine = lines[0];
        if (firstLine.match(/\t/)) {
            headers = firstLine.split('\t');
        } else if (firstLine.match(/;/)) {
            headers = firstLine.split(';');
        } else {
            headers = firstLine.split(',');
        }

        // Map data to headernames
        for (let i = 1, l = lines.length; i < l; i += 1) {
            const line = lines[i];
            let values;
            if (line.match(/\t/)) {
                values = line.split('\t');
            } else if (line.match(/;/)) {
                values = line.split(';');
            } else {
                values = line.split(',');
            }
            const jsonObject = {};
            values.forEach((v, idx) => {
                let val = v;
                if (headers[idx] === 'email') {
                    val = v.toLowerCase();
                }
                jsonObject[headers[idx]] = val;
            });
            jsonData.push(jsonObject);
        }

        const { raceStore } = this.props.stores;
        const { race = {} } = this.props;
        const raceid = race.id;
        raceStore.addMultipleContestants({ id: raceid, classId, contestants: jsonData });
        return true;
    }

