import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import { route } from 'preact-router';
import Markdown from 'preact-markdown';
import linkstate from 'linkstate';

import mu from '../../lib/musher-util';

@observer
class AppRaceDetailClasses extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
		this.inputRef = {};
        this.updateTimer = {};
    }

    render() {
		const { userStore, raceStore, appState } = this.props.stores;
		const { user = {}, teams = [], isAdmin } = userStore;
        const { language = 'en' } = user;

		const { race = {} } = this.props;

        return (<>
            <div class='row'>
                <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-2'>
                    <h2 class='mb-0'><Text id='race.classes'>Klasser</Text></h2>
                </div>
                <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-3 mb-2'>
                    <div class='row'>
                        {race.classes && race.classes.sort((a, b) => a.distance - b.distance).map(currentClass => <>
                            <div class='col-12 col-sm-6 col-lg-4 mb-4'>
                                <div class='border rounded rounded-lg p-3'>
                                    <h5>{currentClass.title}</h5>

                                    {isAdmin && <>
                                        <a href={`/tv/top10/${race.id}/${currentClass.id}`}>TV Top 10</a>
                                    </>}

                                    <small>
                                        {currentClass.startDate && <div>
                                            <span class='font-weight-lighter'><Text id='race.startdate'>Start</Text>:</span> {util.formatDate(`${currentClass.startDate} ${currentClass.startTime || '00:00:00'}`, { locale: language, hour12: false, hour: '2-digit', minute: '2-digit' })}
                                        </div>}
                                        {currentClass.endDate && <div>
                                            <span class='font-weight-lighter'><Text id='race.enddate'>End</Text>:</span> {util.formatDate(`${currentClass.endDate} ${currentClass.endTime || '00:00:00'}`, { locale: language, hour12: false, hour: '2-digit', minute: '2-digit' })}
                                        </div>}
                                    </small>

                                    <div><span class='font-weight-lighter'><Text id='race.distance'>Distanse</Text>:</span> {currentClass.distance} km</div>
                                    <div><span class='font-weight-lighter'><Text id='race.signup'>Påmelding</Text>:</span> {currentClass.signupfee || race.signupfee} NOK</div>
                                    <p class='clearfix'>
                                        <Markdown markdown={mu.replaceImages(currentClass.ingress, race.images, language)} markdownOpts={mu.getMarkdownOptions()} />
                                    </p>
                                    <small>
                                        <Markdown markdown={mu.replaceImages(currentClass.body, race.images, language)} markdownOpts={mu.getMarkdownOptions()} />
                                    </small>
                                    {currentClass.rules && <>
                                        <h5><Text id='race.rules'>Regler</Text></h5>
                                        <Markdown markdown={mu.replaceImages(currentClass.rules, race.images, language)} markdownOpts={mu.getMarkdownOptions()} />
                                    </>}
                                </div>
                            </div>
                        </>)}
                    </div>
                </div>
            </div>
        </>);
    }
}

export default AppRaceDetailClasses;
